import React from "react"
import P5 from "p5"

class Sketch extends React.Component {
    constructor(props) {
        super(props)
        this.myRef = React.createRef()
      }
    
      Sketch = function (p) {
        let b;
      
        p.setup = function () {
          p.createCanvas(100, 100);
          p.background(0);
          b = new Box(p);
        };
        p.draw = function () {
          p.fill(255);
          p.rect(20, 20, 20, 20);
          b.display();
        };
      };
      
    
      componentDidMount() {
        this.myP5 = new P5(this.Sketch, this.myRef.current);
      }
    
      render() {
        return (
          <div>
          <div ref={this.myRef} style={{display: 'grid', justifyContent: 'center', }}></div>
          <p><small>*{this.props.bottomText}</small></p>
          </div>
        )
      }
  }
  
  export default Sketch


  class Box {
    constructor(p) {
      // p is the instance of the sketch that we must call from 
      // this is a namespace for us to utilize 
      this.p = p;
    }
  
    display() {
      this.p.fill(255);
      this.p.rect(0, 0, 20, 20);
    }
  }

  

